.container{
    width: 100%;
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;


}

/* MAIN CONTENT */

.main-container{
    background-color: #333;
    padding: 4em;
    flex: 1;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.content{
    display: flex;
    flex-direction: column;
}

.main-container h2{
    margin-bottom: 2%;
    font-size: 2.2em;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

/* RESPONSIVE */

@media (max-width:1200px){
    main.main-container{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .content{
        align-items: center;
        margin-bottom: 15%;
    }
}