figure {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    animation: slideProfile 1.2s;
}

@keyframes slideProfile {
    from{
        transform: translate(60px);
    }
    to{
        transform: translate(0px);
    }
} 

figure img{
    width: 400px;
    border-radius: 8px;

    margin-bottom: 6%;
    border: 1px #00e6ff solid;
    transition: 0.2s ease-in-out;
}

figure img:hover {
    box-shadow: 0px 0px 6px #00e6ff;
}

/* RESPONSIVE */

@media (max-width: 700px) {
    figure img{
        width: 300px;
    }

}