header {
    background-color: #222;
    padding: 24px;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

header h1{
    font-size: 2.em;
    font-weight: 200;
    cursor: pointer;
}

header nav{
    display: flex;
    align-items: center;
}

header nav a{
    margin-right: 1em;
    margin-left: 1em;
    font-size: 1.5em;
}

header h1, a {
    color:aqua;
    transition: 0.15s ease-in;
}

header a:hover, h1:hover {
    color: white;
}
/* RESPONSIVE */

@media (max-width: 700px) {
    header {
        flex-direction: column;
    }

    header nav {
        margin: 16px;
    }

    header h1 {
        font-size: 2.2em;
    }

    header nav a {
        font-size:1.2em;
    }
}