.card{
    width: 120px;
    height: 120px;
    padding: 10px;
    background-color: #fff;
    border-radius: 8px;
    margin-right: 1em;
    margin-left: 1em;
    margin-top: 12px;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: box-shadow 0.25s ease-in-out;
}

.card img{
    width: 80%;
}

.card:hover{
    box-shadow: 0px 0px 8px #00e0ff;
}

/* RESPONSIVE */ 

@media(max-width:700px){
    .card{
        width: 60px;
        height: 60px;
        
    }
 }